@import url(https://fonts.googleapis.com/css?family=Montserrat);/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before, blockquote:after {
  content: '';
  content: none;
}
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body, a {
  background-color: #000;
  color: #fff;
  font-size: 18px;
  line-height: 1.4;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
}
p {
  margin-top: 10px;
  margin-bottom: 10px;
}
a {
  padding: 0 5px;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.copyright {
  margin-top: 50px;
  font-size: 14px;
}
.container {
  max-width: 800px;
  margin: auto;
  padding: 10px;
}
.container .header {
    margin-top: 30px;
    display: block;
}
.container .header .title {
      font-size: 32px;
      display: inline;
      font-weight: bold;
      margin-right: 10px;
}
.container .header .pers-links {
      display: inline;
}
.container .header .pers-links .link {
        margin-right: 10px;
}
.container .content .intro {
    margin-top: 25px;
}
.container .content .title-section {
    font-size: 24px;
    margin-top: 50px;
}
.container .content .projects {
    margin-top: -25px;
}
.container .content .projects .project {
      margin-top: 50px;
}
.container .content .projects .project .proj-name {
        font-size: 22px;
        font-weight: bold;
}
.container .content .projects .project .proj-category {
        display: inline;
        color: #ffb2b2;
}
.container .content .projects .project .proj-caption {
        display: inline;
        font-style: italic;
}
.container .content .projects .project ul {
        margin-top: 5px;
        margin-left: -5px;
}
.container .content .projects .project ul > li {
        display: inline;
        margin-right: 20px;
}
@media screen and (max-width: 850px) {
.container {
    margin: 0px 20px;
    margin-bottom: 100px;
}
.container .header {
      display: block;
}
.container .header .title {
        margin-bottom: 20px;
}
.container .header .pers-links {
        display: block;
}
.container .header .pers-links .link {
          margin-right: 5px;
}
}
